/* ESTILOS PARA APP */
.App {
  min-height: 100vh;
  background: #010F15;
}

/* ESTILOS PAGINA PRINCIPAL */
.carruselNegro {
  color: rgb(0, 0, 0);
}

.carrusel {
  margin: auto;
  padding: 20px;
}

.cardprin {
  margin-top: 50px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 5px;
}

.cardp {
  background: #ccc;
  transition: background-color .5s;
}

.cardp:hover {
  border: 2px solid #333;
  color: white;
  background: #333;
}

/* ESTILOS JUEGOS */

.contjuegos {
  margin: 20px;
}

.caraJuego {
  margin-top: 25px;
  box-shadow: 3px 3px 3px 4px rgba(0, 0, 0, 0.2);
  background: #ccc;
}
